<template>
  <div class="Menu4 p-3 my-3 border">
    <h6>[Menu4-1.vue]</h6>    
    <!-- <Menu4_Nav />
    <li>출처 : 식품의약품안전처 - 식품영양성분 데이터베이스</li>
    <li>데이터 : 식품영양성분 DB 통합본 (https://various.foodsafetykorea.go.kr/nutrient/)</li> -->
    <!-- <ul class="nav nav-tabs">
      <li class="nav-item" v-for="item in submenu" v-bind:key="item.id">
          <a class="nav-link"><router-link :to="{path:item.router}">{{item.names}}</router-link></a>
      </li>
    </ul> -->
    <button v-for="item in submenu" v-bind:key="item.id" v-on:click="select(item.id)">{{item.names}}</button>
  </div>
  <div class="row">
    <!-- <table class="table table-bordered table-sm">
          <thead> -->
        <table class=".table-sm">
            <thead> 
              <tr>
                  <th v-for="(a,i) in this.resultCol" :key="i">{{this.resultCol[i].col}}</th>
              </tr>
            </thead>
            <tbody>
                <tr v-for="(row,rid) in this.resultVal" :key="rid">
                    <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
                </tr>
            </tbody>
        </table>
  </div>
</template>

<script>
  import Menu4_Nav from './Menu4_Nav';
  import resultJson from './resultVal.json'
  import resultJson_def from './resultVal_def.json'
  var contents_id;
  var resultCol;
  var resultVal;

  export default {
    components: {
              Menu4_Nav: Menu4_Nav
          },
    data() {
              return {
                contents_id, 
                resultCol: resultCol,
                resultVal: resultVal,
                submenu: [
                    {names: '식품영양성분(정제전)', id: 1},
                    {names: '식품영양성분(정제후)', id: 2},
                    {names: '정제 전후 비교', id: 3}
                  ],
            }
          },
    methods: {
      select(item_id) {
        if(item_id == 1) {
          this.resultCol = resultJson[0];
          this.resultVal = resultJson[1];
          // console.log(this.resultCol);
        } else if (item_id == 2) {
          this.resultCol = resultJson_def[0];
          this.resultVal = resultJson_def[1];
          // console.log(this.resultCol)
        }
      }
    }
  }
</script>

