<template>
  <div class="Menu7 p-3 my-3 border">
    <h6>[Menu7-1.vue]</h6>
    <div>
      <button v-for="item in submenu" v-bind:key="item.id" v-on:click="select(item.id)">{{item.names}}</button>
    </div>
  </div>
  <div class="row">
    <!-- <table class="table table-bordered table-sm">
          <thead> -->
        <table class=".table-sm">
            <thead> 
              <tr>
                  <th v-for="(a,i) in this.resultCol" :key="i">{{this.resultCol[i].col}}</th>
              </tr>
            </thead>
            <tbody>
                <tr v-for="(row,rid) in this.resultVal" :key="rid">
                    <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
                </tr>
            </tbody>
        </table>
  </div>
</template>

<script>
  import Menu7_Nav from './Menu7_Nav';
  import resultJson from './resultVal.json' // 원천데이터1
  import resultJson_def from './resultVal_def.json' // 원천데이터2
  var contents_id;
  var resultCol;
  var resultVal;

  export default {
    components: {
              Menu7_Nav: Menu7_Nav
          },
    data() {
              return {
                contents_id, 
                resultCol: this.resultCol,
                resultVal: this.resultVal,
                // resultCol,
                // resultVal,
                submenu: [
                    {names: '식품영양성분', router: '/', id: 1},
                    {names: 'Coffee', router: '/', id: 2},
                    {names: 'HealingFarm', router: '/', id: 3}
                  ],
            }
          },
    methods: {
      select(item_id) {
        if(item_id == 1) {
          this.resultCol = resultJson[0];
          this.resultVal = resultJson[1];
          // console.log(this.resultCol);
        } else if (item_id == 2) {
          this.resultCol = resultJson_def[0];
          this.resultVal = resultJson_def[1];
          // console.log(this.resultCol)
        }
      }
    }
  }
</script>

